.detail {
  padding: 0.2em;
}

.embed_youtube {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.embed_youtube iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.description {
  white-space: pre-wrap;
}

.title {
  display: block;
  overflow: hidden;
  font-weight: 400;
}

.channel {
  display: flex;
  align-items: center;
}

.channelImg {
  margin-right: 0.5em;
  width: 3rem;
  height: 3rem;
  border-radius: 15em;
}

.channelTitle {
  font-weight: 400;
  color: var(--grey-dark-color);
}

:root {
  --white-color: #fff;
  --black-color: #140a00;
  --blue-color: #045fd4;
  --red-color: #ff0000;
  --grey-dark-color: #909090;
  --grey-light-color: #e0e0e0;

  --side-padding: 12px;
  --avatar-size: 36px;

  --font-large: 18px;
  --font-medium: 14px;
  --font-small: 12px;
  --font-micro: 10px;
}
